// ... diğer importlar
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
    Card, CardContent, Typography, CardActions, IconButton, Button, Grid, Box, TextField, Avatar, List, ListItem, ListItemText, Divider, Alert, InputAdornment, Select, MenuItem, Drawer, ListItemIcon
} from '@mui/material';
import ThumbUpAltIcon from '@mui/icons-material/ThumbUpAlt';
import CommentIcon from '@mui/icons-material/Comment';
import WorkOutlineIcon from '@mui/icons-material/WorkOutline';
import { formatDistanceToNow, parseISO } from 'date-fns';
import { useNavigate, Link } from 'react-router-dom';
import { Home, Work, AccountCircle, Chat, ExitToApp, Add } from "@mui/icons-material";
import SearchIcon from '@mui/icons-material/Search';

const axiosInstance = axios.create({
    baseURL: 'http://ec2-44-211-210-149.compute-1.amazonaws.com:8080',
    withCredentials: true, // Çerezlerin gönderilmesini sağlar
});



const Feed = () => {
    const [posts, setPosts] = useState([]);
    const [postComments, setPostComments] = useState({});
    const [commentContents, setCommentContents] = useState({});
    const [currentUser, setCurrentUser] = useState(null);
    const [unreadMessagesCount, setUnreadMessagesCount] = useState(0);
    const [commentCount, setCommentCount] = useState({});
    const [alert, setAlert] = useState({ message: '', severity: '', visible: false });
    const [searchTerm, setSearchTerm] = useState('');
    const [filteredPosts, setFilteredPosts] = useState([]);
    const [selectedCreator, setSelectedCreator] = useState('');
    const [selectedLocation, setSelectedLocation] = useState('');
    const [selectedSkill, setSelectedSkill] = useState('');
    const [friends, setFriends] = useState([]);
    const navigate = useNavigate();
    const [confirmedFriends, setConfirmedFriends] = useState([]);
    const [pendingFriends, setPendingFriends] = useState([]);

    const [searchTermForFriend, setSearchTermForFriend] = useState('');
    const [searchResults, setSearchResults] = useState([]);
    const [unreadFriendMessages, setUnreadFriendMessages] = useState([]);
    const [drawerOpen, setDrawerOpen] = useState(false);  // Drawer kontrolü
    const drawerWidth = 280;

    useEffect(() => {
        const user = JSON.parse(localStorage.getItem('currentUser')) || {};
        if (user && user.username) {
            setCurrentUser(user);
            fetchUnreadMessagesCount(user.username);
        }
    }, []);

    const handleCreateJobPost = () => {
        navigate('/create-job-post');
    };

    const handleLogout = async () => {
        try {
            await axiosInstance.post('/users/logout');
            localStorage.removeItem('currentUser');
            setCurrentUser(null);
            navigate('/login');
            showAlert('Başarıyla Çıkış Yapıldı', 'success');
        } catch (error) {
            console.error('Error logging out:', error);
            showAlert('Error logging out:', 'error');
        }
    };

    const fetchPosts = async () => {
        try {
            const response = await axiosInstance.get('/jobPosts/all');
            setPosts(response.data);
            console.log(response.data);
        } catch (error) {
            console.error('Error fetching posts:', error);
            showAlert('Error fetching posts:', 'error');
        }
    };

    


    const fetchUnreadMessagesCount = async (username) => {
        try {
            const response = await axiosInstance.get(`/messages/unread/count?username=${username}`);
            setUnreadMessagesCount(response.data.count);
        } catch (error) {
            console.error('Error fetching unread messages count:', error);
            showAlert('Error fetching unread messages count', 'error');
        }
    };

    const fetchCommentsByPostId = async (postId) => {
        try {
            const response = await axiosInstance.get(`/comments?postId=${postId}`);
            setPostComments((prevComments) => ({
                ...prevComments,
                [postId]: response.data
            }));
            setCommentCount((prevCount) => ({
                ...prevCount,
                [postId]: response.data.length > 5 ? 5 : response.data.length
            }));
        } catch (error) {
            console.error('Error fetching comments:', error);
            showAlert('Error fetching comments', 'error');
        }
    };

    const handleCommentSubmit = async (postId) => {
        if (!currentUser) {
            showAlert('Yorum Yapmadan Önce Giriş Yapmalısınız', 'error');
            return;
        }

        try {
            await axiosInstance.post('/comments/create', {
                content: commentContents[postId] || '',
                postId: postId
            });
            setCommentContents((prevContents) => ({
                ...prevContents,
                [postId]: ''
            }));
            showAlert('Yorumunuz Başarıyla Gönderildi', 'success');
            fetchCommentsByPostId(postId);
        } catch (error) {
            showAlert('Error Posting Comment', 'error');
            console.error('Error posting comment:', error);
        }
    };

    const handleCommentChange = (postId, content) => {
        setCommentContents((prevContents) => ({
            ...prevContents,
            [postId]: content
        }));
    };

    const handleApply = async (postId) => {
        try {
            await axiosInstance.post(`/jobPosts/${postId}/apply`, {}, { withCredentials: true });
            showAlert('Başvuru Alindi', 'success');
        } catch (error) {
            console.error('Error applying to job post:', error);
            showAlert('Başvururken Hata Oluştu', 'error');
        }
    };
    const handleConfirmFriend = async (friendId) => {
        try {
            const response = await axios.put('http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/friends/confirm-friend', { friendId });
            setPendingFriends(prevPending => prevPending.filter(friend => friend.id !== friendId));
            setConfirmedFriends(prevConfirmed => [...prevConfirmed, response.data]);
            console.log(response.data);
        } catch (error) {
            console.error('Error fetching Friend:', error);
            showAlert('Error fetching Friend:', 'error');
        }
    };
    
    

    const fetchFriend = async () => {
        try {
            const response = await axiosInstance.get('/friends/all');
            setConfirmedFriends(response.data.confirmedFriends);
            setPendingFriends(response.data.pendingFriends);
            setUnreadFriendMessages(response.data.unreadFriendMessages);
            console.log(response.data);
        } catch (error) {
            console.error('Error fetching Friend:', error);
            showAlert('Error fetching Friend:', 'error');
        }
    };
    

    useEffect(() => {
        fetchPosts();
    }, []);

    useEffect(() => {
        fetchFriend();
    }, []);

    useEffect(() => {
        posts.forEach(post => {
            fetchCommentsByPostId(post.id);
        });
    }, [posts]);

    const handleLoadMoreComments = (postId) => {
        setCommentCount((prevCount) => ({
            ...prevCount,
            [postId]: prevCount[postId] + 5
        }));
    };

    const formatRequestDate = (dateArray) => {
        const date = new Date(...dateArray); // Tarih dizisini kullanarak bir Date nesnesi oluştur
        return date.toLocaleString(); // Tarihi istenen formatta döndür
    };
    

    const handleSearchChange = (e) => {
        const term = e.target.value.toLowerCase();
        setSearchTerm(term);
        const newFilteredPosts = posts.filter(post =>
            post.title.toLowerCase().includes(term) ||
            post.skill_names.toLowerCase().includes(term)
        );

        setFilteredPosts(newFilteredPosts);

        // Show an alert with the count of filtered posts
        if (term) {
            showAlert(`"${term}" için ${newFilteredPosts.length} adet post bulundu.`, 'info');
        } else {
            setAlert({ ...alert, visible: false }); // Hide alert if search term is empty
        }
    };

    // Filter posts based on selected filters
    const applyFilters = () => {
        let newFilteredPosts = posts;

        if (selectedCreator) {
            newFilteredPosts = newFilteredPosts.filter(post => post.created_by === selectedCreator);
        }

        if (selectedLocation) {
            newFilteredPosts = newFilteredPosts.filter(post => post.location === selectedLocation);
        }

        if (selectedSkill) {
            newFilteredPosts = newFilteredPosts.filter(post => post.skill_names && post.skill_names.split(',').map(skill => skill.trim()).includes(selectedSkill));
        }

        setFilteredPosts(newFilteredPosts);
    };

    const handleSearchChangeForFriend = (e) => {
        setSearchTermForFriend(e.target.value);
    };

    const handleSearch = async () => {
        try {
            const response = await axios.get(`http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/users/search?username=${searchTermForFriend}`);
            setSearchResults(response.data);
        } catch (error) {
            console.error('Error fetching search results:', error);
        }
    };

    
    

    const handleAddFriend = async (friendId) => {
        try {
            // Mevcut kullanıcı ID'sini almanız gerekiyor. Giriş yapmış kullanıcı ID'sini alın.
            const userId = currentUser.id; // Bunu auth'dan ya da global state'den çekebilirsiniz.
    
            const response = await axios.post('http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/friends/add', null, {
                params: {
                    userId: userId,       // Giriş yapmış kullanıcı ID'si
                    friendId: friendId    // Seçilen arkadaşın ID'si
                }
            });
            showAlert("Arkadaş İsteği Gönderildi.","success")
            window.location.reload();

        } catch (error) {
            showAlert("Arkadaş eklenirken bir hata oluştu. Arkadaş isteği zaten gönderilmiş. ","error")
            console.error("Arkadaş eklenirken bir hata oluştu:", error);
        }
    };
    
    


    useEffect(() => {
        applyFilters();
    }, [selectedCreator, selectedLocation, selectedSkill]);

    const showAlert = (message, severity) => {
        setAlert({ message, severity, visible: true });
        setTimeout(() => {
            setAlert({ message: '', severity: '', visible: false });
        }, 5000); // 5 seconds
    };

    const [expandedPostIds, setExpandedPostIds] = useState({});

    const handleToggleExpand = (postId) => {
        setExpandedPostIds((prevExpandedPostIds) => ({
            ...prevExpandedPostIds,
            [postId]: !prevExpandedPostIds[postId],
        }));
    };

    const MAX_DESCRIPTION_LENGTH = 150; // Açıklama için maksimum karakter sayısı
    const toggleDrawer = (open) => {
        setDrawerOpen(open);
    };
    
    const drawerStyles = {
        "& .MuiListItemText-primary": {
          color: "#ffffff", // Tüm yazılar beyaz
        },
      };

    return (
        <Box sx={{ display: 'flex', height: '100vh' }}>
            {/* Sidebar */}
            <Drawer
                variant="permanent"
                sx={{

                    flexShrink: 0,
                    [`& .MuiDrawer-paper`]: {
                    boxSizing: "border-box",
                    backgroundColor: "#1c0f45",
                    },
                    ...drawerStyles,
                }}
                >
                <Box sx={{ padding: 2, textAlign: "center" }}>
                    <Avatar
                    src={currentUser?.profilePictureUrl || "default-profile.png"}
                    alt={currentUser?.username || "User"}
                    sx={{ width: 80, height: 80, margin: "0 auto" }}
                    />
                    <Typography variant="h6" sx={{ marginTop: 2 }} color="white">
                    {currentUser?.username || "Kullanıcı"}
                    </Typography>
                    <Typography variant="body2" color="white">
                    {currentUser?.email || "example@mail.com"}
                    </Typography>
                </Box>
                <Divider />
                <List>
                    
                    <ListItem button onClick={handleCreateJobPost}>
                    <ListItemIcon>
                        <Add />
                    </ListItemIcon>
                    <ListItemText primary="İş İlanı Ekle" />
                    </ListItem>
                    <Divider />
                    <ListItem button component={Link} to="/profile">
                    <ListItemIcon>
                        <AccountCircle />
                    </ListItemIcon>
                    <ListItemText primary="Profilim" />
                    </ListItem>
                    <Divider />
                    <ListItem button component={Link} to="/my-applications">
                    <ListItemIcon>
                        <Work />
                    </ListItemIcon>
                    <ListItemText primary="Başvurularım" />
                    </ListItem>
                    <Divider />
                    <ListItem button component={Link} to="/my-job-applications">
                    <ListItemIcon>
                        <Home />
                    </ListItemIcon>
                    <ListItemText primary="Verdiğim İlanlar" />
                    </ListItem>
                    <Divider />
                    <ListItem button component={Link} to="/messages">
                    <ListItemIcon>
                        <Chat />
                    </ListItemIcon>
                    <ListItemText primary="Mesajlarım" />
                    {unreadMessagesCount > 0 && (
                        <Typography
                        variant="body2"
                        color="error"
                        sx={{ marginLeft: 1 }}
                        >
                        ({unreadMessagesCount})
                        </Typography>
                    )}
                    </ListItem>
                    <Divider />
                    <ListItem button onClick={handleLogout}>
                    <ListItemIcon>
                        <ExitToApp />
                    </ListItemIcon>
                    <ListItemText primary="Çıkış Yap" />
                    </ListItem>
                </List>
                </Drawer>
    
            {/* Main Content */}
            <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column', padding: 2 }}>
                {/* Search Section */}
                <TextField
                    label="Arama"
                    variant="outlined"
                    value={searchTerm}
                    onChange={handleSearchChange}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                    fullWidth
                    sx={{ mb: 2 }}
                />
                
                {/* Filters */}
                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                    <Select
                        value={selectedCreator}
                        onChange={(e) => setSelectedCreator(e.target.value)}
                        displayEmpty
                        inputProps={{ 'aria-label': 'Filter by Creator' }}
                        sx={{ width: 200 }}
                    >
                        <MenuItem value="">
                            <em>Yazar Seç</em>
                        </MenuItem>
                        {[...new Set(posts.map(post => post.created_by))].map((creator) => (
                            <MenuItem key={creator} value={creator}>{creator}</MenuItem>
                        ))}
                    </Select>
    
                    <Select
                        value={selectedLocation}
                        onChange={(e) => setSelectedLocation(e.target.value)}
                        displayEmpty
                        inputProps={{ 'aria-label': 'Filter by Location' }}
                        sx={{ width: 200 }}
                    >
                        <MenuItem value="">
                            <em>Lokasyon Seç</em>
                        </MenuItem>
                        {[...new Set(posts.map(post => post.location))].map((location) => (
                            <MenuItem key={location} value={location}>{location}</MenuItem>
                        ))}
                    </Select>
    
                    <Select
                        value={selectedSkill}
                        onChange={(e) => setSelectedSkill(e.target.value)}
                        displayEmpty
                        inputProps={{ 'aria-label': 'Filter by Skill' }}
                        sx={{ width: 200 }}
                    >
                        <MenuItem value="">
                            <em>Yetkinlik Seç</em>
                        </MenuItem>
                        {[...new Set(posts.flatMap(post => post.skill_names.split(',')))].map((skill) => (
                            <MenuItem key={skill} value={skill.trim()}>{skill.trim()}</MenuItem>
                        ))}
                    </Select>
                </Box>
    
                {alert.visible && (
                    <Alert severity={alert.severity} sx={{ mb: 2 }}>
                        {alert.message}
                    </Alert>
                )}
    
                {/* Posts Section */}
                <Grid container spacing={3} sx={{ flex: 1 }}>
                    {(filteredPosts.length > 0 ? filteredPosts : posts).map((post) => (
                        <Grid item xs={12} key={post.id}>
                            <Card sx={{ border: '1px solid #ccc', boxShadow: 3, mb: 3 }}>
                                <CardContent>
                                    {/* User avatar */}
                                    <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                                        <Avatar
                                            src={post.user?.profilePictureUrl || 'default-profile.png'}
                                            alt={post.user?.username || 'User'}
                                            sx={{ width: 40, height: 40 }}
                                        />
                                    </Box>
                                    {/* Post title */}
                                    <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
                                        <Typography variant="h5" component="div">
                                            {post.title}
                                        </Typography>
                                    </Box>
    
                                    {/* Post creator */}
                                    <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                                        <Typography sx={{ mb: 1.5 }} color="text.secondary">
                                            Created by: 
                                            <Link to={`/users/username/${post.created_by}`} color="primary" underline="hover">
                                                {post.created_by}
                                            </Link>
                                        </Typography>
                                    </Box>
    
                                    {/* Post location */}
                                    <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                                        <Typography sx={{ mb: 1.5 }} color="text.secondary">
                                            Location: {post.location}
                                        </Typography>
                                    </Box>
    
                                    {/* Skills */}
                                    <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
                                        <Typography variant="body2" color="text.secondary">
                                            Skills:
                                        </Typography>
                                        <Box component="ul" sx={{ pl: 2 }}>
                                            {post.skill_names ? (
                                                post.skill_names.split(',').map((skill, index) => (
                                                    <Typography key={index} component="li" variant="body2" color="text.secondary">
                                                        {skill.trim()}
                                                    </Typography>
                                                ))
                                            ) : (
                                                <Typography variant="body2" color="text.secondary">No skills listed</Typography>
                                            )}
                                        </Box>
                                    </Box>
    
                                    {/* Post content */}
                                    <Typography variant="body2" color="text.secondary">
                                        {expandedPostIds[post.id] || post.description.length <= MAX_DESCRIPTION_LENGTH
                                            ? post.description.split('\n').map((line, index) => (
                                                <Typography key={index} paragraph>
                                                    {line}
                                                </Typography>
                                            ))
                                            : post.description.split('\n').slice(0, MAX_DESCRIPTION_LENGTH).map((line, index) => (
                                                <Typography key={index} paragraph>
                                                    {line}
                                                </Typography>
                                            ))}
                                    </Typography>
    
                                    {/* Show more button */}
                                    {post.description.length > MAX_DESCRIPTION_LENGTH && (
                                        <Button onClick={() => handleToggleExpand(post.id)}>
                                            {expandedPostIds[post.id] ? 'Gizle' : 'Devamını Göster'}
                                        </Button>
                                    )}
    
                                    <Box sx={{ mt: 2, display: 'flex', justifyContent: 'space-between' }}>
                                        <Typography variant="caption" color="text.secondary">
                                        İlan Tarihi : {new Date(post.timestamp).toLocaleDateString()}
                                            {post.createdBy}
                                        </Typography>
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            startIcon={<WorkOutlineIcon />}
                                            onClick={() => handleApply(post.id)}
                                        >
                                            Başvur
                                        </Button>
                                    </Box>
    
                                    {/* Comments section */}
                                        <CardContent>
                                            <TextField
                                                label="Yorum Giriniz.."
                                                variant="outlined"
                                                fullWidth
                                                value={commentContents[post.id] || ''}
                                                onChange={(e) => handleCommentChange(post.id, e.target.value)}
                                            />
                                            <Button onClick={() => handleCommentSubmit(post.id)} variant="contained" color="primary">
                                                Yorum Gönder
                                            </Button>

                                            <Box mt={2}>
                                                {postComments[post.id] && postComments[post.id].length > 0 ? (
                                                    <Box sx={{ border: '1px solid #ccc', borderRadius: 2, p: 2 }}>
                                                        {postComments[post.id].slice(0, commentCount[post.id]).map((comment) => (
                                                            <Box
                                                                key={comment.id}
                                                                sx={{
                                                                    border: '1px solid #e0e0e0',
                                                                    borderRadius: 1,
                                                                    padding: 1,
                                                                    marginBottom: 1,
                                                                    backgroundColor: '#f9f9f9',
                                                                    display: 'flex',
                                                                    alignItems: 'center',
                                                                }}
                                                            >
                                                                <Avatar
                                                                    src={comment.profilePictureUrl || 'default-profile.png'}
                                                                    alt={comment.userName}
                                                                    sx={{ width: 30, height: 30, marginRight: 1 }}
                                                                />
                                                                <Box>
                                                                    <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                                                                        {comment.userName}
                                                                    </Typography>
                                                                    <Typography variant="body2">{comment.content}</Typography>
                                                                    <Typography variant="caption" color="text.secondary">
                                                                        {new Date(comment.createdAt).toLocaleString()} {/* Yorumun tarihi burada gösteriliyor */}
                                                                    </Typography>
                                                                </Box>
                                                            </Box>
                                                        ))}
                                                        {commentCount[post.id] < postComments[post.id].length && (
                                                            <Button onClick={() => handleLoadMoreComments(post.id)} variant="text">
                                                                Daha Fazla Yorum Yükle
                                                            </Button>
                                                        )}
                                                    </Box>
                                                ) : (
                                                    <Typography variant="body2" color="text.secondary">
                                                        Bu post için henüz yorum yok.
                                                    </Typography>
                                                )}
                                            </Box>
                                        </CardContent>

                                </CardContent>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
{/* Sağdaki Arkadaşlar Drawer'ı */}
<Box sx={{ display: 'flex', height: '100vh' }}>
            {/* Sağ Arkadaşlar Drawer'ı */}
            <Drawer
                variant="permanent"
                anchor="right"
                sx={{
                    flexShrink: 0,
                    [`& .MuiDrawer-paper`]: {
                        boxSizing: "border-box",
                        backgroundColor: "#1c0f45",
                    },
                    ...drawerStyles,
                }}
            >
                <Typography variant="h6" sx={{ textAlign: 'center', mb: 2 } } color="white">Arkadaşlar</Typography>

                {/* Search Bar for Adding New Friends */}
                <Box display="flex" color="white">
                    <TextField
                        label="Kullanıcı Ara"
                        variant="outlined"
                        value={searchTermForFriend}
                        onChange={handleSearchChangeForFriend}
                        fullWidth
                        sx={{ mb: 2 }}
                        color="white"
                    />
                    <IconButton onClick={handleSearch} color="primary" sx={{ ml: 1 }}>
                        <SearchIcon />
                    </IconButton>
                </Box>

                {/* Search Results */}
                {searchResults.length > 0 && (
                    <List>
                        {searchResults.map(result => (
                            <ListItem key={result.id} sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                                <div style={{ display: 'flex', alignItems: 'center' }}>
                                    <Avatar src={result.profilePictureUrl || 'default-profile.png'} alt={result.username} sx={{ marginRight: 2 }} />
                                    <ListItemText
                                        primary={
                                            <Link to={`/users/username/${result.username}`} style={{ color: 'primary' }} color="white">
                                                {result.username}
                                            </Link>
                                        }
                                    />
                                </div>
                                <Button
                                    onClick={() => handleAddFriend(result.id)}
                                    variant="contained"
                                    color="primary"
                                >
                                    Ekle
                                </Button>
                            </ListItem>
                        ))}
                    </List>
                )}

                {/* Confirmed Friends */}
                <Typography variant="h6" sx={{ textAlign: 'center', mb: 2 }} color="white">Onaylı Arkadaşlar</Typography>
                <List>
                    {confirmedFriends.map(friend => {
                        const isSender = friend.senderUser.username === currentUser.username;
                        const displayUser = isSender ? friend.receiverUser : friend.senderUser;
                        const hasUnreadMessage = unreadFriendMessages.some((msg) => {
                            return msg.senderUsername === friend.receiverUser.username;
                        });

                        return (
                            <ListItem key={friend.id} sx={{ display: 'flex', alignItems: 'center' }}>
                                {hasUnreadMessage && (
                                    <Link to="/messages">
                                        <img src="https://cdn1.iconfinder.com/data/icons/aami-flat-emails/64/email-41-256.png" alt="unread-message" width={30} />
                                    </Link>
                                )}
                                <Avatar
                                    src={displayUser?.profilePictureUrl || 'default-profile.png'}
                                    alt={displayUser?.username || 'User'}
                                    sx={{ marginRight: 2 }}
                                />
                                <ListItemText
                                    primary={
                                        <Link to={`/users/username/${displayUser.username}`} style={{ color: "primary" }}>
                                            {displayUser.username}
                                        </Link>
                                    }
                                />
                            </ListItem>
                        );
                    })}
                </List>

                {/* Pending Friends */}
                <Typography variant="h6" sx={{ textAlign: 'center', mb: 2 }} color="white">Onay Bekleyenler</Typography>
                <List>
                    {pendingFriends.map(friend => (
                        <ListItem key={friend.id} sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                            <div style={{ display: 'flex', alignItems: 'center' }}>
                                <Avatar
                                    src={friend.senderUser?.profilePictureUrl || 'default-profile.png'}
                                    alt={friend.senderUser?.username || 'User'}
                                    sx={{ marginRight: 2 }}
                                />
                                <ListItemText
                                    primary={
                                        <Link to={`/users/username/${friend.senderUser.username}`} style={{ color: "primary" }}>
                                            {friend.senderUser.username}
                                        </Link>
                                    }
                                />
                            </div>
                            <Button onClick={() => handleConfirmFriend(friend.id)} variant="contained" color="primary">
                                +
                            </Button>
                        </ListItem>
                    ))}
                </List>
            </Drawer>

            {/* Main Content */}
            <Box sx={{ flexGrow: 1, padding: 3 }}>
                <Typography variant="h4">Profil</Typography>
                {/* Profil detayları burada */}
            </Box>
        </Box>
        </Box>
        </Box>
    );
    
};

export default Feed;