import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Box, TextField, Button, Typography } from '@mui/material';

const Profile = () => {
    const [user, setUser] = useState({
        username: '',
        email: '',
        profilePictureUrl: '',
        password: '',
        cvLink: '',
        educations: [],
        experiences: [],
        skills: []
    });

    useEffect(() => {
        const fetchUserProfile = async () => {
            const currentUser = JSON.parse(localStorage.getItem('currentUser'));
            console.log("Current User from localStorage:", currentUser); // Log currentUser

            if (currentUser && currentUser.email) {
                try {
                    const response = await axios.get(`http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/users/${currentUser.email}`);
                    console.log("User data fetched:", response.data); // Log fetched user data
                    setUser({
                        username: response.data.username || '',
                        email: response.data.email || '',
                        profilePictureUrl: response.data.profilePictureUrl || '',
                        password: '', // Password is usually not displayed
                        cvLink: response.data.cvLink || '',
                        educations: response.data.educations || [],
                        experiences: response.data.experiences || [],
                        skills: response.data.skills || []
                    });
                } catch (error) {
                    console.error("Error fetching user data:", error); // Log error
                }
            } else {
                console.error("No user found in localStorage or email is missing.");
            }
        };

        fetchUserProfile();
    }, []);

    const handleUpdate = async () => {
        try {
            await axios.put('http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/users/update', user, {
                headers: {
                    'Content-Type': 'application/json', // Set the correct Content-Type
                }
            });
            alert('Profile updated successfully');
        } catch (error) {
            console.error("Error updating profile:", error); // Log error
            alert('Failed to update profile');
        }
    };

    return (
        <Box sx={{ padding: 2 }}>
            <Typography variant="h4">Profilim</Typography>
            <TextField
                label="Username"
                value={user.username || ''} // Use empty string if null
                onChange={(e) => setUser({ ...user, username: e.target.value })}
                fullWidth
                margin="normal"
            />
            <TextField
                label="Email"
                value={user.email || ''} // Use empty string if null
                fullWidth
                margin="normal"
                disabled // Keep email disabled
            />
            <TextField
                label="Profile Resmi"
                value={user.profilePictureUrl || ''} // Use empty string if null
                onChange={(e) => setUser({ ...user, profilePictureUrl: e.target.value })}
                fullWidth
                margin="normal"
            />
            <TextField
                label="Şifre"
                type="password" // Password input
                value={user.password || ''} // Use empty string if null
                onChange={(e) => setUser({ ...user, password: e.target.value })}
                fullWidth
                margin="normal"
            />
            <TextField
                label="CV Link"
                value={user.cvLink || ''} // Use empty string if null
                onChange={(e) => setUser({ ...user, cvLink: e.target.value })}
                fullWidth
                margin="normal"
            />

            {/* Display educations */}
            <Typography variant="h6" mt={2}>Eğitim Bilgileri</Typography>
            {user.educations.map((education, index) => (
                <TextField
                    key={index}
                    label={`Education ${index + 1}`}
                    value={education.schoolName || ''} // Use empty string if null
                    onChange={(e) => {
                        const updatedEducations = [...user.educations];
                        updatedEducations[index].schoolName = e.target.value;
                        setUser({ ...user, educations: updatedEducations });
                    }}
                    fullWidth
                    margin="normal"
                />
            ))}

            {/* Display experiences */}
            <Typography variant="h6" mt={2}>Deneyimler</Typography>
            {user.experiences.map((experience, index) => (
                <TextField
                    key={index}
                    label={`Experience ${index + 1}`}
                    value={experience.position || ''} // Use empty string if null
                    onChange={(e) => {
                        const updatedExperiences = [...user.experiences];
                        updatedExperiences[index].position = e.target.value;
                        setUser({ ...user, experiences: updatedExperiences });
                    }}
                    fullWidth
                    margin="normal"
                />
            ))}

            {/* Display skills */}
            <Typography variant="h6" mt={2}>Yetenekler</Typography>
            {user.skills.map((skill, index) => (
                <TextField
                    key={index}
                    label={`Skill ${index + 1}`}
                    value={skill.skillName || ''} // Use empty string if null
                    onChange={(e) => {
                        const updatedSkills = [...user.skills];
                        updatedSkills[index].skillName = e.target.value;
                        setUser({ ...user, skills: updatedSkills });
                    }}
                    fullWidth
                    margin="normal"
                />
            ))}

            <Button variant="contained" color="primary" onClick={handleUpdate}>
                Update Profile
            </Button>
        </Box>
    );
};

export default Profile;
