import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
    Box,
    Typography,
    TextField,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    Modal,
    Alert,
    IconButton
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/material/styles';


const axiosInstance = axios.create({
    baseURL: 'http://ec2-44-211-210-149.compute-1.amazonaws.com:8080',
    withCredentials: true,
});

const BlinkingRow = styled(TableRow)(({ theme, shouldBlink }) => ({
    backgroundColor: shouldBlink ? '#4caf50' : 'transparent',
    animation: shouldBlink ? 'blink-animation 1s steps(1, end) infinite' : 'none',
    '@keyframes blink-animation': {
        '50%': {
            backgroundColor: 'transparent',
        },
        '100%': {
            backgroundColor: '#4caf50',
        },
    },
}));

const Messages = () => {
    const [messages, setMessages] = useState([]);
    const [messageContent, setMessageContent] = useState('');
    const [recipientUsername, setRecipientUsername] = useState('');
    const [selectedSender, setSelectedSender] = useState(null);
    const [openModal, setOpenModal] = useState(false);
    const [alert, setAlert] = useState({ message: '', severity: '', visible: false });
    const [currentMessageId, setCurrentMessageId] = useState(null);

    const fetchMessages = async () => {
        const currentUser = JSON.parse(localStorage.getItem('currentUser'));
        const username = currentUser.username;

        try {
            const response = await axios.get(`http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/messages`, {
                params: { username },
            });
            setMessages(response.data);
        } catch (error) {
            console.error('Error fetching messages:', error);
            showAlert('Error fetching messages', 'error');
        }
    };

    const handleSendMessage = async () => {
        const currentUser = JSON.parse(localStorage.getItem('currentUser'));
        const senderUsername = currentUser.username;
        const finalRecipient = selectedSender || recipientUsername;

        if (!finalRecipient || !messageContent) {
            showAlert('Recipient and message content are required', 'error');
            return;
        }

        try {
            await axiosInstance.post('/messages/send', {
                senderUsername,
                recipientUsername: finalRecipient,
                content: messageContent,
            });

            setMessageContent('');
            setRecipientUsername('');
            fetchMessages();
            handleModalClose();
            showAlert('Mesaj Gönderildi', 'success');
        } catch (error) {
            console.error('Error sending message:', error);
            showAlert('Kişi arkadaş listesinde değil', 'error');
        }
    };

    const handleRowClick = async (sender) => {
        const filteredMessages = messages.filter(msg =>
            msg.senderUsername === sender || msg.recipientUsername === sender
        );

        if (filteredMessages.length > 0) {
            const lastMessage = filteredMessages[filteredMessages.length - 1];
            setCurrentMessageId(lastMessage.id);
            await markMessagesAsRead(lastMessage.id);
        }

        setSelectedSender(sender);
        setOpenModal(true);
        setMessageContent('');
    };

    const markMessagesAsRead = async (id) => {
        try {
            await axiosInstance.put(`/messages/${id}/read`);
            fetchMessages();
        } catch (error) {
            console.error('Error marking message as read:', error);
        }
    };

    const handleModalClose = () => {
        setOpenModal(false);
        setSelectedSender(null);
        setMessageContent('');
        setCurrentMessageId(null);
    };

    useEffect(() => {
        fetchMessages();
    }, []);

    const groupedMessages = messages.reduce((acc, msg) => {
        const currentUser = JSON.parse(localStorage.getItem('currentUser'));
        const username = currentUser.username;

        if (msg.senderUsername === username) {
            return acc;
        }

        const key = msg.senderUsername === username
            ? msg.recipientUsername
            : msg.senderUsername;

        if (!acc[key]) {
            acc[key] = { ...msg, lastMessage: msg.content, lastReadStatus: msg.readStatus };
        } else {
            acc[key].lastMessage = msg.content;
            acc[key].lastReadStatus = msg.readStatus;
        }
        return acc;
    }, {});

    const formatTimestamp = (timestampArray) => {
        const [year, month, day, hour, minute] = timestampArray;
        const date = new Date(year, month - 1, day, hour, minute);
        return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    };
    const showAlert = (message, severity) => {
        setAlert({ message, severity, visible: true });
        setTimeout(() => {
            setAlert({ message: '', severity: '', visible: false });
        }, 5000); // 2 seconds
    };

    return (
        <Box sx={{ padding: 2 }}>
                        {alert.visible && (
                    <Alert variant="filled" severity={alert.severity} sx={{ mb: 2 }}>
                        {alert.message}
                    </Alert>
                )}
            <Typography variant="h4" gutterBottom>Mesajlarım</Typography>
            
            <TableContainer component={Paper} sx={{ marginBottom: 3 }}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Gönderen</TableCell>
                            <TableCell>Son Mesaj İçeriği</TableCell>
                            <TableCell>Okundu</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {Object.entries(groupedMessages).map(([sender, msg]) => (
                            <BlinkingRow
                                key={msg.id}
                                onClick={() => handleRowClick(sender)}
                                shouldBlink={msg.lastReadStatus === "false"} // Only blink if unread
                                style={{ cursor: 'pointer' }}
                            >
                                <TableCell>{sender}</TableCell>
                                <TableCell>{msg.lastMessage}</TableCell>
                                <TableCell>{msg.lastReadStatus === "true" ? 'Evet' : 'Hayır'}</TableCell>
                            </BlinkingRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

            {/* Spacing between table and message inputs */}
            <Box sx={{ marginBottom: 3 }}>
                <TextField
                    label="Kime Mesaj Atmak İstersin?"
                    variant="outlined"
                    value={recipientUsername}
                    onChange={(e) => setRecipientUsername(e.target.value)}
                    fullWidth
                    sx={{ marginBottom: 2 }}
                />
                <TextField
                    label="Mesaj İçeriği"
                    variant="outlined"
                    value={messageContent}
                    onChange={(e) => setMessageContent(e.target.value)}
                    fullWidth
                    multiline
                    rows={4}
                    sx={{ marginBottom: 2 }}
                />
                <Button onClick={handleSendMessage} variant="contained" color="primary">Gönder</Button>
            </Box>

            <Modal open={openModal} onClose={handleModalClose}>
                <Box sx={{ p: 4, bgcolor: 'white', borderRadius: 2, maxWidth: 600, margin: 'auto', marginTop: '10%', overflowY: 'scroll', maxHeight: '70vh' }}>
                    <IconButton onClick={handleModalClose} style={{ float: 'right' }}>
                        <CloseIcon />
                    </IconButton>
                    <Typography variant="h6">{selectedSender} ile konuşma</Typography>
                    <Box sx={{ maxHeight: '50vh', overflowY: 'scroll', marginBottom: 2 }}>
                        {messages.filter(msg => (msg.senderUsername === selectedSender || msg.recipientUsername === selectedSender)).map((msg) => (
                            <Box 
                                key={msg.id} 
                                sx={{ 
                                    display: 'flex', 
                                    justifyContent: msg.senderUsername === JSON.parse(localStorage.getItem('currentUser')).username ? 'flex-end' : 'flex-start',
                                    marginBottom: 1 
                                }}
                            >
                                <Box 
                                    sx={{ 
                                        bgcolor: msg.senderUsername === JSON.parse(localStorage.getItem('currentUser')).username ? 'green' : 'gray',
                                        color: 'white',
                                        padding: 1,
                                        borderRadius: 1,
                                        maxWidth: '80%',
                                        wordBreak: 'break-word'
                                    }}
                                >
                                    <Typography variant="body2">{msg.content} - {formatTimestamp(msg.timestamp)}</Typography>
                                </Box>
                            </Box>
                        ))}
                    </Box>
                    <TextField
                        label="Cevap Yaz"
                        variant="outlined"
                        value={messageContent}
                        onChange={(e) => setMessageContent(e.target.value)}
                        fullWidth
                        multiline
                        rows={2}
                    />
                    <Button onClick={handleSendMessage} variant="contained" color="primary">Gönder</Button>
                </Box>
            </Modal>
        </Box>
    );
};

export default Messages;
