import React, { useEffect, useState } from 'react';
import axios from 'axios';
import {
  Box, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper
} from '@mui/material';

const axiosInstance = axios.create({
    baseURL: 'http://ec2-44-211-210-149.compute-1.amazonaws.com:8080',
    withCredentials: true
});

const MyApplications = () => {
    const [applications, setApplications] = useState([]);

    const fetchApplications = async () => {
        try {
            const response = await axiosInstance.get('/jobApplication/my-applications');
            setApplications(response.data);
            console.log(response.data);
        } catch (error) {
            console.error('Error fetching applications:', error);
        }
    };

    useEffect(() => {
        fetchApplications();
    }, []);

    return (
        <Box sx={{ padding: 2 }}>
            <Typography variant="h4" gutterBottom>
                Başvurularım
            </Typography>
            <TableContainer component={Paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Post Title</TableCell>
                            <TableCell>Created By</TableCell>
                            <TableCell>Application Date</TableCell>
                            <TableCell>Status</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                            {applications.map((application) => {
                                // applyDate'yi uygun formata dönüştürme
                                const applyDateArray = application.applyDate; // Tarih dizisi
                                const applyDate = new Date(
                                    applyDateArray[0], // Yıl
                                    applyDateArray[1] - 1, // Ay (0 tabanlı)
                                    applyDateArray[2], // Gün
                                    applyDateArray[3], // Saat
                                    applyDateArray[4], // Dakika
                                    applyDateArray[5] // Saniye
                                ).toLocaleDateString(); // Yerel tarih formatında gösterme

                                return (
                                    <TableRow key={application.id}> <TableCell>{application.jobPost.title}</TableCell>
                                    <TableCell>{application.jobPost.createdBy}</TableCell>
                                    <TableCell>{applyDate}</TableCell> {/* Dönüştürülmüş tarih */}
                                    <TableCell>{application.applicationStatus}</TableCell> </TableRow>
                                );
                            })}
                        </TableBody>
                </Table>
            </TableContainer>
        </Box>
    );
};

export default MyApplications;
