import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { TextField, Button, Grid, Typography, Box, Paper, MenuItem } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const CreateJobPost = () => {
    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');
    const [workType, setWorkType] = useState('Full-time');
    const [location, setLocation] = useState('');
    const [positionLevel, setPositionLevel] = useState('Senior');
    const [requiredSkills, setRequiredSkills] = useState([{ skillName: '' }]);
    const [applicationCount] = useState(0); // Başlangıçta 0
    const [companyId] = useState(1); // Örnek Company ID
    const [currentUser, setCurrentUser] = useState(null); // Giriş yapan kullanıcı bilgileri
    const navigate = useNavigate();

    useEffect(() => {
        const user = JSON.parse(localStorage.getItem('currentUser'));
        console.log('Fetched user from localStorage:', user); // Konsola yazdır
        if (user && user.email) {
            setCurrentUser(user);
        } else {
            alert('Kullanıcı giriş yapmamış. Lütfen giriş yapın.');
            navigate('/login');
        }
    }, [navigate]);

    const handleAddSkill = () => {
        setRequiredSkills([...requiredSkills, { skillName: '' }]); // Yeni bir yetenek alanı ekle
    };

    const handleSkillChange = (index, value) => {
        const skills = [...requiredSkills];
        skills[index].skillName = value; // Belirtilen indeksteki yeteneği güncelle
        setRequiredSkills(skills);
    };

    const handleLoginAndSubmit = async (e) => {
        const user = JSON.parse(localStorage.getItem('currentUser'));
        if (user) {
            const loginData = {
                email: user.email,
                password: user.password // Password'u buradan alıyorsanız, localStorage'da saklandığından emin olun
            };

            
        } else {
            alert('Kullanıcı bilgileri bulunamadı. Lütfen tekrar giriş yapın.');
            navigate('/login');
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
    
    
        const jobPost = {
            title,
            description,
            workType,
            location,
            positionLevel,
            requiredSkills,
            applicationCount,
            company: { id: companyId },
            createdBy: currentUser.id // Kullanıcı ID'sini burada kullan
        };
    
        console.log('Job Post to be created:', jobPost); // Gönderilen iş ilanını kontrol et
    
        try {
            // Gerekli başlıkları ekleyin
            const response = await axios.post('http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/jobPosts/create', jobPost, {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${currentUser.token}` // Token kullanıyorsanız burayı ayarlayın
                }
            });
            alert('Job Post created successfully');
            navigate('/feed');
        } catch (error) {
            console.error('Error creating job post:', error.response ? error.response.data : error.message);
            alert('Failed to create job post: ' + (error.response ? error.response.data : error.message));
        }
    };
    
    

    return (
        <Grid container justifyContent="center" alignItems="center" style={{ minHeight: '100vh' }}>
            <Grid item xs={12} md={6}>
                <Paper elevation={3} sx={{ padding: 3 }}>
                    <Typography variant="h4" align="center" gutterBottom>
                        İş İlanı Ekle
                    </Typography>
                    <Box component="form" onSubmit={handleSubmit} sx={{ mt: 2 }}>
                        <TextField
                            label="Başlık"
                            variant="outlined"
                            fullWidth
                            margin="normal"
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            required
                        />
                        <TextField
                            label="Açıklama"
                            variant="outlined"
                            fullWidth
                            multiline
                            rows={4}
                            margin="normal"
                            value={description}
                            onChange={(e) => setDescription(e.target.value)}
                            required
                        />
                        <TextField
                            label="Çalışma Türü"
                            variant="outlined"
                            fullWidth
                            select
                            margin="normal"
                            value={workType}
                            onChange={(e) => setWorkType(e.target.value)}
                        >
                            <MenuItem value="Full-time">Full-time</MenuItem>
                            <MenuItem value="Part-time">Part-time</MenuItem>
                        </TextField>
                        <TextField
                            label="Konum"
                            variant="outlined"
                            fullWidth
                            margin="normal"
                            value={location}
                            onChange={(e) => setLocation(e.target.value)}
                            required
                        />
                        <TextField
                            label="Pozisyon Seviyesi"
                            variant="outlined"
                            fullWidth
                            select
                            margin="normal"
                            value={positionLevel}
                            onChange={(e) => setPositionLevel(e.target.value)}
                        >
                            <MenuItem value="Junior">Junior</MenuItem>
                            <MenuItem value="Senior">Senior</MenuItem>
                        </TextField>
                        {requiredSkills.map((skill, index) => (
                            <TextField
                                key={index}
                                label={`Gereken Yetenek ${index + 1}`}
                                variant="outlined"
                                fullWidth
                                margin="normal"
                                value={skill.skillName}
                                onChange={(e) => handleSkillChange(index, e.target.value)}
                                required
                            />
                        ))}
                        <Button onClick={handleAddSkill} variant="outlined" color="primary" sx={{ mt: 2 }}>
                            Yetenek Ekle
                        </Button>
                        <Button type="submit" variant="contained" color="primary" fullWidth sx={{ mt: 3 }}>
                            İş İlanı Oluştur
                        </Button>
                    </Box>
                </Paper>
            </Grid>
        </Grid>
    );
};

export default CreateJobPost;
