import React, { useState } from 'react';
import { TextField, Button, IconButton, Grid, Typography, Paper } from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const Register = () => {
    const navigate = useNavigate();
    const [formData, setFormData] = useState({
        email: '',
        password: '',
        username: '',
        userType: 'JOB_SEEKER',
        experiences: [{ position: '', companyName: '', startDate: '', endDate: '', reasonForLeaving: '' }],
        educations: [{ schoolName: '', startDate: '', graduationDate: '', gpa: '' }],
        skills: [{ skillName: '' }],
        projects: [{ projectTitle: '', description: '' }],
        references: [{ name: '', phoneNumber: '', emailAddress: '' }],
        profilePictureUrl: '',
        cvLink: ''
    });
    const [errors, setErrors] = useState({});

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value
        });
    };

    const handleArrayChange = (e, index, field, arrayName) => {
        const { value } = e.target;
        const updatedArray = formData[arrayName].map((item, i) => i === index ? { ...item, [field]: value } : item);
        setFormData({
            ...formData,
            [arrayName]: updatedArray
        });
    };

    const handleArrayAdd = (arrayName, initialItem) => {
        setFormData({
            ...formData,
            [arrayName]: [...formData[arrayName], initialItem]
        });
    };

    const handleArrayRemove = (index, arrayName) => {
        const updatedArray = formData[arrayName].filter((_, i) => i !== index);
        setFormData({
            ...formData,
            [arrayName]: updatedArray
        });
    };

    const validateForm = () => {
        const newErrors = {};
        if (!formData.email) newErrors.email = 'Email is required';
        if (!formData.username) newErrors.username = 'Username is required';
        if (!formData.password) newErrors.password = 'Password is required';

        formData.experiences.forEach((exp, index) => {
            if (!exp.position) newErrors[`experience_position_${index}`] = 'Position is required';
            if (!exp.companyName) newErrors[`experience_companyName_${index}`] = 'Company Name is required';
        });

        formData.educations.forEach((edu, index) => {
            if (!edu.schoolName) newErrors[`education_schoolName_${index}`] = 'School Name is required';
            if (!edu.startDate) newErrors[`education_startDate_${index}`] = 'Start Date is required';
            if (!edu.graduationDate) newErrors[`education_graduationDate_${index}`] = 'Graduation Date is required';
        });

        formData.skills.forEach((skill, index) => {
            if (!skill.skillName) newErrors[`skill_skillName_${index}`] = 'Skill is required';
        });

        formData.projects.forEach((project, index) => {
            if (!project.projectTitle) newErrors[`project_projectTitle_${index}`] = 'Project Title is required';
            if (!project.description) newErrors[`project_description_${index}`] = 'Description is required';
        });

        formData.references.forEach((ref, index) => {
            if (!ref.name) newErrors[`reference_name_${index}`] = 'Reference Name is required';
            if (!ref.phoneNumber) newErrors[`reference_phoneNumber_${index}`] = 'Phone Number is required';
            if (!ref.emailAddress) newErrors[`reference_emailAddress_${index}`] = 'Email Address is required';
        });

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (validateForm()) {
            try {
                await axios.post('http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/users/register', formData, {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                });
                navigate('/login');
            } catch (err) {
                setErrors({ ...errors, server: 'Failed to register' });
                navigate('/login');
            }
        }
    };

    return (
        <Paper elevation={3} style={{ padding: '2rem', maxWidth: '800px', margin: '2rem auto' }}>
            <Typography variant="h4" gutterBottom>Register</Typography>
            {errors.server && <Typography color="error">{errors.server}</Typography>}
            <form onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <TextField
                            label="Email"
                            name="email"
                            type="email"
                            fullWidth
                            value={formData.email}
                            onChange={handleChange}
                            error={!!errors.email}
                            helperText={errors.email}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            label="Username"
                            name="username"
                            fullWidth
                            value={formData.username}
                            onChange={handleChange}
                            error={!!errors.username}
                            helperText={errors.username}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            label="Password"
                            name="password"
                            type="password"
                            fullWidth
                            value={formData.password}
                            onChange={handleChange}
                            error={!!errors.password}
                            helperText={errors.password}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            label="Profile Picture URL"
                            name="profilePictureUrl"
                            fullWidth
                            value={formData.profilePictureUrl}
                            onChange={handleChange}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            label="CV Upload Link"
                            name="cvLink"
                            fullWidth
                            value={formData.cvLink}
                            onChange={handleChange}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="h6">Work Experiences</Typography>
                        {formData.experiences.map((exp, index) => (
                            <div key={index}>
                                <TextField
                                    label="Position"
                                    value={exp.position}
                                    onChange={(e) => handleArrayChange(e, index, 'position', 'experiences')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`experience_position_${index}`]}
                                    helperText={errors[`experience_position_${index}`]}
                                />
                                <TextField
                                    label="Company Name"
                                    value={exp.companyName}
                                    onChange={(e) => handleArrayChange(e, index, 'companyName', 'experiences')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`experience_companyName_${index}`]}
                                    helperText={errors[`experience_companyName_${index}`]}
                                />
                                <TextField
                                    label="Start Date"
                                    type="date"
                                    value={exp.startDate}
                                    onChange={(e) => handleArrayChange(e, index, 'startDate', 'experiences')}
                                    fullWidth
                                    margin="normal"
                                    InputLabelProps={{ shrink: true }}
                                    error={!!errors[`experience_startDate_${index}`]}
                                    helperText={errors[`experience_startDate_${index}`]}
                                />
                                <TextField
                                    label="End Date"
                                    type="date"
                                    value={exp.endDate}
                                    onChange={(e) => handleArrayChange(e, index, 'endDate', 'experiences')}
                                    fullWidth
                                    margin="normal"
                                    InputLabelProps={{ shrink: true }}
                                />
                                <TextField
                                    label="Reason for Leaving"
                                    value={exp.reasonForLeaving}
                                    onChange={(e) => handleArrayChange(e, index, 'reasonForLeaving', 'experiences')}
                                    fullWidth
                                    margin="normal"
                                />
                                {index > 0 && (
                                    <IconButton
                                        onClick={() => handleArrayRemove(index, 'experiences')}
                                        color="error"
                                    >
                                        <RemoveCircleIcon />
                                    </IconButton>
                                )}
                                <IconButton
                                    onClick={() => handleArrayAdd('experiences', { position: '', companyName: '', startDate: '', endDate: '', reasonForLeaving: '' })}
                                    color="primary"
                                >
                                    <AddCircleIcon />
                                </IconButton>
                            </div>
                        ))}
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="h6">Educations</Typography>
                        {formData.educations.map((education, index) => (
                            <div key={index}>
                                <TextField
                                    label="School Name"
                                    value={education.schoolName}
                                    onChange={(e) => handleArrayChange(e, index, 'schoolName', 'educations')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`education_schoolName_${index}`]}
                                    helperText={errors[`education_schoolName_${index}`]}
                                />
                                <TextField
                                    label="Start Date"
                                    type="date"
                                    value={education.startDate}
                                    onChange={(e) => handleArrayChange(e, index, 'startDate', 'educations')}
                                    fullWidth
                                    margin="normal"
                                    InputLabelProps={{ shrink: true }}
                                    error={!!errors[`education_startDate_${index}`]}
                                    helperText={errors[`education_startDate_${index}`]}
                                />
                                <TextField
                                    label="Graduation Date"
                                    type="date"
                                    value={education.graduationDate}
                                    onChange={(e) => handleArrayChange(e, index, 'graduationDate', 'educations')}
                                    fullWidth
                                    margin="normal"
                                    InputLabelProps={{ shrink: true }}
                                    error={!!errors[`education_graduationDate_${index}`]}
                                    helperText={errors[`education_graduationDate_${index}`]}
                                />
                                <TextField
                                    label="GPA"
                                    value={education.gpa}
                                    onChange={(e) => handleArrayChange(e, index, 'gpa', 'educations')}
                                    fullWidth
                                    margin="normal"
                                />
                                {index > 0 && (
                                    <IconButton
                                        onClick={() => handleArrayRemove(index, 'educations')}
                                        color="error"
                                    >
                                        <RemoveCircleIcon />
                                    </IconButton>
                                )}
                                <IconButton
                                    onClick={() => handleArrayAdd('educations', { schoolName: '', startDate: '', graduationDate: '', gpa: '' })}
                                    color="primary"
                                >
                                    <AddCircleIcon />
                                </IconButton>
                            </div>
                        ))}
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="h6">Skills</Typography>
                        {formData.skills.map((skill, index) => (
                            <div key={index}>
                                <TextField
                                    label="Skill"
                                    value={skill.skillName}
                                    onChange={(e) => handleArrayChange(e, index, 'skillName', 'skills')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`skill_skillName_${index}`]}
                                    helperText={errors[`skill_skillName_${index}`]}
                                />
                                {index > 0 && (
                                    <IconButton
                                        onClick={() => handleArrayRemove(index, 'skills')}
                                        color="error"
                                    >
                                        <RemoveCircleIcon />
                                    </IconButton>
                                )}
                                <IconButton
                                    onClick={() => handleArrayAdd('skills', { skillName: '' })}
                                    color="primary"
                                >
                                    <AddCircleIcon />
                                </IconButton>
                            </div>
                        ))}
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="h6">Projects</Typography>
                        {formData.projects.map((project, index) => (
                            <div key={index}>
                                <TextField
                                    label="Project Title"
                                    value={project.projectTitle}
                                    onChange={(e) => handleArrayChange(e, index, 'projectTitle', 'projects')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`project_projectTitle_${index}`]}
                                    helperText={errors[`project_projectTitle_${index}`]}
                                />
                                <TextField
                                    label="Description"
                                    value={project.description}
                                    onChange={(e) => handleArrayChange(e, index, 'description', 'projects')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`project_description_${index}`]}
                                    helperText={errors[`project_description_${index}`]}
                                />
                                {index > 0 && (
                                    <IconButton
                                        onClick={() => handleArrayRemove(index, 'projects')}
                                        color="error"
                                    >
                                        <RemoveCircleIcon />
                                    </IconButton>
                                )}
                                <IconButton
                                    onClick={() => handleArrayAdd('projects', { projectTitle: '', description: '' })}
                                    color="primary"
                                >
                                    <AddCircleIcon />
                                </IconButton>
                            </div>
                        ))}
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="h6">References</Typography>
                        {formData.references.map((ref, index) => (
                            <div key={index}>
                                <TextField
                                    label="Reference Name"
                                    value={ref.name}
                                    onChange={(e) => handleArrayChange(e, index, 'name', 'references')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`reference_name_${index}`]}
                                    helperText={errors[`reference_name_${index}`]}
                                />
                                <TextField
                                    label="Phone Number"
                                    value={ref.phoneNumber}
                                    onChange={(e) => handleArrayChange(e, index, 'phoneNumber', 'references')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`reference_phoneNumber_${index}`]}
                                    helperText={errors[`reference_phoneNumber_${index}`]}
                                />
                                <TextField
                                    label="Email Address"
                                    value={ref.emailAddress}
                                    onChange={(e) => handleArrayChange(e, index, 'emailAddress', 'references')}
                                    fullWidth
                                    margin="normal"
                                    error={!!errors[`reference_emailAddress_${index}`]}
                                    helperText={errors[`reference_emailAddress_${index}`]}
                                />
                                {index > 0 && (
                                    <IconButton
                                        onClick={() => handleArrayRemove(index, 'references')}
                                        color="error"
                                    >
                                        <RemoveCircleIcon />
                                    </IconButton>
                                )}
                                <IconButton
                                    onClick={() => handleArrayAdd('references', { name: '', phoneNumber: '', emailAddress: '' })}
                                    color="primary"
                                >
                                    <AddCircleIcon />
                                </IconButton>
                            </div>
                        ))}
                    </Grid>
                    <Grid item xs={12}>
                        <Button type="submit" variant="contained" color="primary">Register</Button>
                    </Grid>
                </Grid>
            </form>
        </Paper>
    );
};

export default Register;
