import React from 'react';
import { Avatar, Box, Typography, MenuItem } from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';

const axiosInstance = axios.create({
    baseURL: 'http://localhost:8080',
    withCredentials: true
});

const Sidebar = ({ currentUser, unreadMessagesCount, handleLogout }) => {
    const navigate = useNavigate();

    const handleCreateJobPost = () => {
        navigate('/create-job-post');
    };

    return (
        <Box sx={{ width: 250, padding: 2, position: 'fixed', top: 0, left: 0, height: '100%', backgroundColor: '#f5f5f5', boxShadow: 3 }}>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 4 }}>
                <Avatar
                    src={currentUser.profilePictureUrl || 'default-profile.png'}
                    alt={currentUser.username || 'User'}
                    sx={{ width: 60, height: 60, marginRight: 2 }}
                />
                <Typography variant="h6">
                    {currentUser.username || 'Default User'}
                </Typography>
            </Box>
            <MenuItem onClick={handleCreateJobPost}>İş İlanı Ekle</MenuItem>
            <MenuItem component={Link} to="/profile">Profilim</MenuItem>
            <MenuItem component={Link} to="/my-applications">Başvurularım</MenuItem>
            <MenuItem component={Link} to="/messages">
                Mesajlarım
                {unreadMessagesCount > 0 && (
                    <span style={{ color: 'red', marginLeft: 5 }}>({unreadMessagesCount})</span>
                )}
            </MenuItem>
            <MenuItem onClick={handleLogout}>Çıkış Yap</MenuItem>
        </Box>
    );
};

export default Sidebar;
