import React from 'react';
import { Box } from '@mui/material';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import { Link } from 'react-router-dom';
import { Add, AccountCircle, Work, Home, Chat, ExitToApp } from '@mui/icons-material';

const drawerWidth = 240;

const Layout = ({ children }) => {
    return (
        <Box sx={{ display: 'flex' }}>
            {/* Sidebar */}
            <Drawer
                variant="permanent"
                sx={{
                    width: drawerWidth,
                    flexShrink: 0,
                    [`& .MuiDrawer-paper`]: {
                        width: drawerWidth,
                        boxSizing: "border-box",
                        backgroundColor: "#1c0f45",
                        color: "#fff",
                    },
                }}
            >
                <List>
                    <ListItem button component={Link} to="/feed">
                        <ListItemIcon><Home sx={{ color: '#fff' }} /></ListItemIcon>
                        <ListItemText primary="Anasayfa" />
                    </ListItem>
                    <ListItem button component={Link} to="/create-job-post">
                        <ListItemIcon><Add sx={{ color: '#fff' }} /></ListItemIcon>
                        <ListItemText primary="İş İlanı Ekle" />
                    </ListItem>
                    <ListItem button component={Link} to="/profile">
                        <ListItemIcon><AccountCircle sx={{ color: '#fff' }} /></ListItemIcon>
                        <ListItemText primary="Profilim" />
                    </ListItem>
                    <ListItem button component={Link} to="/my-applications">
                        <ListItemIcon><Work sx={{ color: '#fff' }} /></ListItemIcon>
                        <ListItemText primary="Başvurularım" />
                    </ListItem>
                    <ListItem button component={Link} to="/my-job-applications">
                        <ListItemIcon><Home sx={{ color: '#fff' }} /></ListItemIcon>
                        <ListItemText primary="Verdiğim İlanlar" />
                    </ListItem>
                    <ListItem button component={Link} to="/messages">
                        <ListItemIcon><Chat sx={{ color: '#fff' }} /></ListItemIcon>
                        <ListItemText primary="Mesajlarım" />
                    </ListItem>
                    <ListItem button>
                        <ListItemIcon><ExitToApp sx={{ color: '#fff' }} /></ListItemIcon>
                        <ListItemText primary="Çıkış Yap" />
                    </ListItem>
                </List>
            </Drawer>

            {/* İçerik */}
            <Box sx={{ flexGrow: 1, padding: 3 }}>
                {children}
            </Box>
        </Box>
    );
};

export default Layout;
