import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
    Box, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper,
    Dialog, DialogActions, DialogContent, DialogTitle, Button, TextField, List, ListItem
} from '@mui/material';
import { useNavigate } from 'react-router-dom';

const axiosInstance = axios.create({
    baseURL: 'http://localhost:8080',
    withCredentials: true
});

const VerilenIlanlar = () => {
    const [applications, setApplications] = useState([]);
    const [currentUser, setCurrentUser] = useState(null);
    const [openModal, setOpenModal] = useState(false);
    const [selectedApplication, setSelectedApplication] = useState(null);
    const [newStatus, setNewStatus] = useState('');
    const navigate = useNavigate();

    useEffect(() => {
        const user = JSON.parse(localStorage.getItem('currentUser')) || {};
        if (user && user.username) {
            setCurrentUser(user);
            fetchApplicationsForMyJobPosts();
        }
    }, []);

    const fetchApplicationsForMyJobPosts = async () => {
        try {
            const response = await axiosInstance.get('/jobApplication/my-job-posts/applications');
            setApplications(response.data);
            console.log(response.data);
        } catch (error) {
            console.error('Error fetching applications:', error);
        }
    };

    const handleRowClick = (application) => {
        setSelectedApplication(application);
        setNewStatus(application.applicationStatus); // Set initial status to current application status
        setOpenModal(true);
    };

    const handleCloseModal = () => {
        setOpenModal(false);
        setSelectedApplication(null);
    };


    const handleStatusChange = async () => {
        try {
            await axiosInstance.put(
                `/jobApplication/update/${selectedApplication.id}`,
                { applicationStatus: newStatus }, // Burada sadece gerekli alanı gönderiyoruz
                { headers: { 'Content-Type': 'application/json' } }
            );
            fetchApplicationsForMyJobPosts(); // Refresh the list after updating
            handleCloseModal(); // Close modal after update
        } catch (error) {
            console.error('Error updating application status:', error);
        }
    };
    
    

    return (
        <Box sx={{ display: 'flex', height: '100vh' }}>
            <Box sx={{ width: 250, backgroundColor: '#f4f4f4', padding: 2 }}>
                <List>
                    <ListItem button>
                        <Typography variant="h6">Verdiğim İlanlar</Typography>
                    </ListItem>
                </List>
            </Box>

            <Box sx={{ flexGrow: 1, padding: 2 }}>
                <Typography variant="h4" gutterBottom>
                    Verdiğim İlanlara Başvurular
                </Typography>
                <TableContainer component={Paper}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>İlan Başlığı</TableCell>
                                <TableCell>Başvuran</TableCell>
                                <TableCell>Durum</TableCell>
                                <TableCell>Başvuru Tarihi</TableCell>
                                <TableCell>Güncelleme Tarihi</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {applications.map((application) => {
                                // applyDate'yi uygun formata dönüştürme
                                const applyDateArray = application.applyDate; // Tarih dizisi
                                const updateArray = application.updateTime; // Tarih dizisi
                                const applyDate = new Date(
                                    applyDateArray[0], // Yıl
                                    applyDateArray[1] - 1, // Ay (0 tabanlı)
                                    applyDateArray[2], // Gün
                                    applyDateArray[3], // Saat
                                    applyDateArray[4], // Dakika
                                    applyDateArray[5] // Saniye
                                ).toLocaleDateString(); // Yerel tarih formatında gösterme
                                const applyUpdate = new Date(
                                    updateArray[0], // Yıl
                                    updateArray[1] - 1, // Ay (0 tabanlı)
                                    updateArray[2], // Gün
                                    updateArray[3], // Saat
                                    updateArray[4], // Dakika
                                    updateArray[5] // Saniye
                                ).toLocaleDateString(); // Yerel tarih formatında gösterme

                                return (
                                    <TableRow key={application.id} onClick={() => handleRowClick(application)}>
                                        <TableCell>{application.jobPost.title}</TableCell>
                                        <TableCell>{application.user.username}</TableCell>
                                        <TableCell>{application.applicationStatus}</TableCell>
                                        <TableCell>{applyDate}</TableCell> {/* Dönüştürülmüş tarih */}
                                        <TableCell>{applyUpdate}</TableCell> {/* Dönüştürülmüş tarih */}
                                    </TableRow>
                                );
                            })}
                        </TableBody>
                    </Table>
                </TableContainer>

                {/* Modal for Changing Status */}
                <Dialog open={openModal} onClose={handleCloseModal}>
                    <DialogTitle>Başvuru Durumunu Değiştir</DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            label="Yeni Durum"
                            type="text"
                            fullWidth
                            variant="outlined"
                            value={newStatus}
                            onChange={(e) => setNewStatus(e.target.value)}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleCloseModal} color="primary">
                            İptal
                        </Button>
                        <Button onClick={handleStatusChange} color="primary">
                            Kaydet
                        </Button>
                    </DialogActions>
                </Dialog>
            </Box>
        </Box>
    );
};

export default VerilenIlanlar;
