// src/App.js
import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Login from './pages/Login';
import Register from './pages/Register';
import Feed from './pages/Feed';
import Profile from './pages/Profile';
import CreateJobPost from './pages/CreateJobPost'; // Yeni bileşeni ekleyin
import UserProfile from './pages/UserProfile'; // Yeni bileşeni ekleyin
import MyApplications from './pages/MyApplications'; // Import the new component
import Messages from './pages/Messages';
import Sidebar from './pages/Sidebar'; // Sidebar'ı import ediyoruz
import VerilenIlanlar from './pages/VerilenIlanlar';
import Layout from './pages/Layout';


const App = () => {
    return (
        <Router>
            <div className="app">
            <Layout>
                <Routes>
                    <Route path="/login" element={<Login />} />
                    <Route path="/register" element={<Register />} />
                    <Route path="/feed" element={<Feed />} />
                    <Route path="/profile" element={<Profile />} /> {/* Add this route */}
                    <Route path="/create-job-post" element={<CreateJobPost />} /> {/* Yeni yönlendirme */}
                    <Route path="/users/username/:username" element={<UserProfile />} /> {/* Düzenlendi */}
                    <Route path="/" element={<Login />} /> {/* Redirect to login by default */}
                    <Route path="/my-applications" element={<MyApplications />} /> {/* Add this line */}
                    <Route path="/my-job-applications" element={<VerilenIlanlar />} />
                    <Route path="/messages" element={<Messages />} />
                </Routes>
            </Layout>
            </div>
        </Router>
    );
};

export default App;
