import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import {
    Box,
    Typography,
    Avatar,
    Button,
    Card,
    CardContent,
    CardActions,
    Modal,
    TextField,
    IconButton,
    Alert
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';

// Axios instance oluşturulması
const axiosInstance = axios.create({
    baseURL: 'http://ec2-44-211-210-149.compute-1.amazonaws.com:8080',
    withCredentials: true
});

const UserProfile = () => {
    const { username } = useParams();
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);
    const [openModal, setOpenModal] = useState(false);
    const [messageContent, setMessageContent] = useState('');
    const [alert, setAlert] = useState({ message: '', severity: '', visible: false });

    useEffect(() => {
        const fetchUser = async () => {
            try {
                const response = await axiosInstance.get(`/users/username/${username}`);
                setUser(response.data);
            } catch (error) {
                console.error('Error fetching user:', error);
            } finally {
                setLoading(false);
            }
        };

        fetchUser();
    }, [username]);

    const handleSendMessage = async () => {
        if (!messageContent) {
            showAlert('Mesaj içeriği boş olamaz.', 'error');
            return;
        }

        const currentUser = JSON.parse(localStorage.getItem('currentUser'));
        const senderUsername = currentUser.username;

        try {
            await axiosInstance.post('/messages/send', {
                senderUsername,
                recipientUsername: user.username,
                content: messageContent,
            });

            setMessageContent('');
            handleModalClose();
            showAlert('Mesaj gönderildi.', 'success');
        } catch (error) {
            console.error('Error sending message:', error);
            showAlert('Mesaj gönderme hatası.', 'error');
        }
    };

    const showAlert = (message, severity) => {
        setAlert({ message, severity, visible: true });
        setTimeout(() => {
            setAlert({ message: '', severity: '', visible: false });
        }, 5000); // 5 saniye sonra kaybolacak
    };

    const handleModalClose = () => {
        setOpenModal(false);
        setMessageContent('');
    };

    if (loading) {
        return <Typography>Loading...</Typography>;
    }

    if (!user) {
        return <Typography>User not found.</Typography>;
    }

    return (
        <Box sx={{ padding: 2, display: 'flex', justifyContent: 'center' }}>
            <Card sx={{ width: 300 }}>
                <CardContent>
                    <Avatar
                        src={user.profilePictureUrl || 'default-profile.png'}
                        alt={user.username}
                        sx={{ width: 100, height: 100, margin: '0 auto' }}
                    />
                    <Typography variant="h5" align="center">{user.username}</Typography>
                    <Typography variant="body2" color="text.secondary" align="center">{user.email}</Typography>
                    <Typography variant="body2" align="center">Durum: {user.status}</Typography>
                </CardContent>
                <CardActions>
                    <Button variant="contained" color="primary" onClick={() => setOpenModal(true)} fullWidth>
                        Mesaj Gönder
                    </Button>
                </CardActions>
            </Card>

            {/* Alert Message */}
            {alert.visible && (
                <Alert variant="filled" severity={alert.severity} sx={{ mb: 2 }}>
                    {alert.message}
                </Alert>
            )}

            {/* Modal for sending message */}
            <Modal open={openModal} onClose={handleModalClose}>
                <Box sx={{ p: 4, bgcolor: 'white', borderRadius: 2, maxWidth: 400, margin: 'auto', marginTop: '20%' }}>
                    <IconButton onClick={handleModalClose} style={{ float: 'right' }}>
                        <CloseIcon />
                    </IconButton>
                    <Typography variant="h6">Mesaj Gönder: {user.username}</Typography>
                    <TextField
                        label="Mesaj İçeriği"
                        variant="outlined"
                        value={messageContent}
                        onChange={(e) => setMessageContent(e.target.value)}
                        fullWidth
                        multiline
                        rows={4}
                        sx={{ marginBottom: 2 }}
                    />
                    <Button onClick={handleSendMessage} variant="contained" color="primary" fullWidth>
                        Gönder
                    </Button>
                </Box>
            </Modal>
        </Box>
    );
};

export default UserProfile;
