import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { TextField, Button, Container, Typography, Box, Alert } from '@mui/material';
import { styled } from '@mui/system';

const LoginContainer = styled(Container)(({ theme }) => ({
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
    backgroundColor: '#f5f5f5',
}));

const LoginForm = styled('form')(({ theme }) => ({
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
    maxWidth: '400px',
    padding: theme.spacing(3),
    backgroundColor: '#fff',
    boxShadow: '0px 4px 12px rgba(0, 0, 0, 0.1)',
    borderRadius: theme.shape.borderRadius,
}));

const Login = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post('http://ec2-44-211-210-149.compute-1.amazonaws.com:8080/users/login', {
                email: email,
                password: password
            }, {
                withCredentials: true
            });
            if (response.status === 200) {
                const loggedInUser = response.data; // Artık burada User nesnesi var
                console.log('Login successful, User:', loggedInUser);
    
                // Kullanıcı bilgilerini localStorage'a kaydet
                localStorage.setItem('currentUser', JSON.stringify({
                    id: loggedInUser.id,
                    username: loggedInUser.username,
                    email: loggedInUser.email,
                    password: loggedInUser.password,
                    profilePictureUrl: loggedInUser.profilePictureUrl, // Profil resmi URL'si ekleniyor
                }));
    
                navigate('/feed');
            }
        } catch (err) {
            setError('Login failed');
            console.error('Login error:', err);
        }
        console.log('currentUser after login:', JSON.parse(localStorage.getItem('currentUser')));
    };

    return (
        <LoginContainer maxWidth="sm">
            <LoginForm onSubmit={handleSubmit}>
                <Typography variant="h4" gutterBottom>
                    Giriş Yap
                </Typography>
                {error && <Alert severity="error">{error}</Alert>}
                <TextField
                    label="E-mail"
                    type="email"
                    variant="outlined"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    fullWidth
                    margin="normal"
                    required
                />
                <TextField
                    label="Şifre"
                    type="password"
                    variant="outlined"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    fullWidth
                    margin="normal"
                    required
                />
                <Box mt={2}>
                    <Button type="submit" variant="contained" color="primary" fullWidth>
                        Giriş
                    </Button>
                </Box>
                <Box mt={2}>
                    <Button
                        variant="outlined"
                        color="secondary"
                        fullWidth
                        onClick={() => navigate('/register')}
                    >
                        Kayıt Ol
                    </Button>
                </Box>
            </LoginForm>
        </LoginContainer>
    );
};

export default Login;
